@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600&display=swap);
* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
  font-size: 100%;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FFFFFF;
  font-size: 14px;
  font-weight: 400;
}

*::-webkit-scrollbar {
  width: 8px;
}
*::-webkit-scrollbar-track {
  background: var(--PRIMARY-BACKGROUND-COLOR);
}
*::-webkit-scrollbar-thumb {
  background-color: var(--ACCENT);
  border-radius: 20px;
  border: 0 solid #ffffff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  --PRIMARY-BACKGROUND-COLOR: #ffffff;
  --PRIMARY-TEXT-COLOR: #2e2e2e;
  --PRIMARY-GREY: #a1a1a1;
  --SECONDARY-GREY: #5a5a5a;
  --ACCENT: #0d5994;
}

html[data-theme="dark"] {
  --PRIMARY-BACKGROUND-COLOR: #101010;
  --PRIMARY-TEXT-COLOR: #ffffff;
  --PRIMARY-GREY: #a1a1a1;
  --SECONDARY-GREY: #5a5a5a;
  --ACCENT: #74b9ff;
}

.App {
  min-height: 100vh;
  background-color: #ffffff;
  background-color: var(--PRIMARY-BACKGROUND-COLOR);
  color: #2e2e2e;
  color: var(--PRIMARY-TEXT-COLOR);
  transition: all .2s ease-in-out;
}

.container {
  max-width: 1024px;
  min-height: 100vh;
  padding: 100px 0 100px 200px;
}

@media all and (max-width: 1280px) {
  .container {
    padding: 2.5vh 1.8vw 0 4vw;
  }
}

.themer {
  outline: none;
  border: none;
  padding: 12px;
  cursor: pointer;
  background: #2e2e2e;
  background: var(--PRIMARY-TEXT-COLOR);
  color: #ffffff;
  color: var(--PRIMARY-BACKGROUND-COLOR);
  font-weight: 300;
  font-size: 16px;
  position: fixed;
  z-index: 9999;
  bottom: 20px;
  right: 50px;
}

a,
a:visited,
a:active,
a.bar {
  text-decoration: none;
  color: inherit;
  color: #0d5994;
  color: var(--ACCENT);
  transition: all 0.1s ease-in-out;
}

a.bar {
  position: relative;
  overflow: hidden;
  display: inline-flex;
}

a.bar:after {
  content: '';
  position: absolute;
  display: inline-block;
  height: 1px;
  width: 100%;
  bottom: 0;
  left: -100%;
  opacity: 1;
  background: #0d5994;
  background: var(--ACCENT);
  transition: all .1s ease-in-out;
}

a.bar:hover:after {
  opacity: 1;
  left: 0;
}
.profile-image {
  width: 280px;
  height: auto;
  border-radius: 50%;
}
.lead-container {
  margin: 30px 0;
}

.title {
  font-family: 'Cormorant Garamond';
  font-size: 38px;
  font-weight: 600;
}

.description {
  margin-top: 8px;
  margin-bottom: 8px;
}

.separator {
  color: var(--PRIMARY-GREY);
  margin: 0 5px;
}

.mh-10 {
  margin-left: 5px;
  margin-right: 5px;
}
.say-hello {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 50px;
}

.say-hello h3 {
  font-size: 20px;
  font-weight: 600;
}

.hand-icon {
  width: 30px;
  height: 30px;
}

p {
  font-size: 18px;
  line-height: 150%;
  padding: 18px 0;
}

@media all and (max-width: 1280px) {
  p {
    font-size: 14px;
  }
}

.heart-icon {
  margin: 0 4px;
  color: #c0392b;
}

ul.list-expanded {
  margin-left: 8px;
}

ul.list-expanded li {
  list-style-type: "-";
  padding-left: 10px;
}

ul.social {
  display: flex;
}

ul.social li {
  list-style-type: none;
  margin-top: 20px;
  width: 150px;
  margin-right: 5px;
  
}

ul.social li a{
  position: relative;
  text-align: center;
  padding: 6px 30px;
  border: 1px solid var(--ACCENT);
  background: var(--PRIMARY-BACKGROUND-COLOR);
  overflow: hidden;
  z-index: 1;
}

ul.social li a:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: var(--PRIMARY-TEXT-COLOR);
  z-index: -1;
  transition: all 0.1s ease-in-out;
}

@media all and (max-width: 1280px) {
  ul.social li a:after {
    display: none;
  }
}

ul.social li a:hover:after {
  top: 5px;
  left: 5px;
}

ul.social li:nth-of-type(1) {
  margin-left: 0;
}

.inline-icon {
  line-height: 200%;
  margin: 0 1px;
  display: inline-flex;
}

